      // 
      hmwActiveNum1: 0 || sessionStorage.getItem("hmwFlIndex1"),
      // 2--
      hmwActiveNum4: 0 || sessionStorage.getItem("hmwFlIndex2"),
      // 
      hmwActiveNum2: 0,
      // 
      hmwActiveNum3: 0,
      //    
      value: 0,
      switch1: false,
      switch2: false,
      // 
      hmwFl: [
        {
          id: 1,
          name: "",
          parent_id: 0,
          child: [
            {
              id: 1,
              name: "",
            },
            {
              id: 2,
              name: "",
            },
            {
              id: 3,
              name: "",
            },
            {
              id: 4,
              name: "",
            },
            {
              id: 5,
              name: "",
            },
          ],
        },
        {
          id: 2,
          name: "",
          parent_id: 0,
          child: [
            {
              id: 7,
              name: "",
            },
            {
              id: 8,
              name: "",
            },
            {
              id: 9,
              name: "",
            },
            {
              id: 12,
              name: "",
            },
            {
              id: 13,
              name: "",
            },
          ],
        },
      ],
      //   
      hmwSort: ["", "", "", "", ""],
      // 
      hmwChoose: [
        // { type: 0, value: "" },
        // { type: 2, value: "" },
        // { type: 3, value: "" },
        // { type: 4, value: "" },
        // { type: 5, value: "" },
        // { type: 7, value: "" },
        // { type: 8, value: "" },
        // { type: 9, value: "" },
        // { type: 10, value: "" },
        // { type: 11, value: "" }
      ],
      // 
      hmwList: [
        {
          title: "1622",
          date: "032618:30~032215:00|",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
        {
          title: "",
          date: "326123456789087",
          name: "",
          number: 134,
          price: 0,
          course: 5,
          img:
            "http://baijiayun-wangxiao.oss-cn-beijing.aliyuncs.com/uploads/avatar.jpg",
        },
      ],
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    // 
    onConfirm() {
      this.$refs.item.toggle();
    },
    onConfirm1() {
      this.$refs.item1.toggle();
    },
    onConfirm2() {
      this.$refs.item2.toggle();
    },
    // ,
    async hmwGetNav(navCan = "", listCan = 0) {
      console.log(listCan)
      let { data } = await this.$Net.courseNav(navCan);
      let { data: list } = await this.$Net.courseList({ 
        params:{
         order_by:listCan,
         course_type:'',
        }
      });
      console.log(list);
      // 
      this.hmwChoose = data.data.appCourseType;
      // 
      this.hmwList = list.data.list;
    },
    // -------------------------------------------
    // idid
    hmwFlD(i, fuI) {
      console.log(i, fuI);
      // 
      if (fuI == 1) {
        // 
        this.hmwActiveNum1 = i.id;
        sessionStorage.setItem("hmwFlIndex1", i.id);
      } else {
        this.hmwActiveNum4 = i.id;
        sessionStorage.setItem("hmwFlIndex2", i.id);
      }

      // 
      // this.hmwGetNav('attr_val_id='+i.id)
      // 
    },
    // -----------------------------------------------------------
    hmwOk() {
      // 
      this.onConfirm();
    },
    // -----------------------------------------------------------
    hmwRefresh() {
      this.hmwActiveNum1 = 0;
      this.hmwActiveNum4 = 0;
      // this.hmwActiveNum4 = this.hmwFl[0].child
      sessionStorage.removeItem("hmwFlIndex1");
      sessionStorage.removeItem("hmwFlIndex2");
      // 
      this.onConfirm();
    },
    // ----------------------------------------------------------------
    async HmwSort(i) {
      // 
      this.hmwActiveNum2 = i;
      this.hmwGetNav('',i)
// let { data: list } = await this.$Net.courseList({order_by:i});
      // 
      this.onConfirm1();
    },
    // ---------------------------------------------------------
    // ,
    async HmwChoose(i,index) {
      console.log(i)
      // 
      this.hmwActiveNum3 = index;
      // 
      this.onConfirm2();
      // 
      let { data: list } = await this.$Net.courseList({ 
        params:{
         course_type:i.id,
        }
      });
      this.hmwList = list.data.list;
    },
    // 
    hmwJump(item){
      console.log(item.id)
      // 
      sessionStorage.setItem('hmwPath',JSON.stringify({path:'/about',name:'About'}))
      sessionStorage.setItem('hmwXQ',JSON.stringify(item))
      // id
      sessionStorage.setItem('hmwXQid',JSON.stringify(item.id))
      this.$router.push('/detail')
    }
  },
  mounted() {
    this.hmwGetNav();
  },
};
</script>

<style scoped>

